<template>
  <button :class="`sl-button sl-button--${type}`">
    <slot></slot>
  </button>
</template>
<script>
export default {
  name: "index",
  data(){
    return {}
  },
  props: {
    type: {
      type: String,
      default: "default"
    }
  }
}
</script>
<style lang="scss">
  @import "@/assets/styles/variable.scss";
  .sl-button {
    box-sizing: border-box;
    display: inline-block;
    padding: 12px 20px;
    margin: 0;
    font-size: 14px;
    font-weight: 500;
    text-align: center;
    white-space: nowrap;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
    background: $background-default;
    border: 1px solid $border-default;
    border-radius: 4px;
    outline: none;
    transition: .1s;

    -webkit-appearance: none;
    &:hover,
    &:active,
    &:focus {
      color: $primary-color;
      background: $hover-color;
      border-color: $primary-color;
      outline: none;
    }
    &.sl-button--primary {
      color: $active-color;
      background-color: $primary-color;
      border-color: $primary-color;
      &:active,
      &:hover {
        background: $primary-active;
        border-color: $primary-active;
        outline: none;
      }
    }
    &.sl-button--success {
      color: $active-color;
      background: $success-color;
      border-color: $success-color;
      outline: none;
    }
    &.sl-button--info {
      color: $active-color;
      background: $info-color;
      border-color: $info-color;
      outline: none;
      &:hover,
      &:focus,
      &:active {
        background: $info-hover;
        border-color: $info-hover;
        outline: none;
      }
    }
    &.sl-button--warning {
      color: $active-color;
      background: $warning-color;
      border-color: $warning-color;
      outline: none;
      &:hover,
      &:focus,
      &:active {
        background: $warning-hover;
        border-color: $warning-hover;
        outline: none;
      }
    }
    &.sl-button--danger {
      color: $active-color;
      background: $danger-color;
      border-color: $danger-color;
      outline: none;
      &:hover,
      &:focus,
      &:active {
        background: $danger-hover;
        border-color: $danger-hover;
        outline: none;
      }
    }
  }

</style>


