<template>
  <div class="button">
    <h3 class="title">Button 按钮</h3>
    <p class="small-title">常用的操作按钮</p>
    <div class="widget span6">
      <div class="widget-title">基础用法</div>
      <div class="widget-body"> 
        <div>
          <sl-button>默认按钮</sl-button>
          <sl-button type="primary">主要按钮</sl-button>
          <sl-button type="success">成功按钮</sl-button>
          <sl-button type="info">信息按钮</sl-button>
          <sl-button type="warning">警告按钮</sl-button>
          <sl-button type="danger">危险按钮</sl-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import slButton from "@/components/basicPage/button"
export default {
  name: "Button",
  data(){
    return {}
  },
  components: {
    slButton
  }
}
</script>
<style lang="scss" scoped>
  .button {
    .title {
      margin: 13px 0 8px;
      font-family: myriadpro-light;
      font-size: 24px;
      font-weight: 400 !important;
    }
    .small-title {
      margin: 13px 0 8px;
      font-family: myriadpro-light;
      font-size: 18px;
    }
  }

</style>


